.flip {
  height: 65px;
  overflow: hidden;
  --tw-text-opacity: 1;
  color: rgb(255 255 255 / var(--tw-text-opacity));
}

@media (min-width: 768px) {

  .flip {
    height: 6rem;
  }
}
@keyframes show {

  0% {
    margin-top: -360px;
  }

  5% {
    margin-top: -240px;
  }

  33% {
    margin-top: -240px;
  }

  38% {
    margin-top: -120px;
  }

  66% {
    margin-top: -120px;
  }

  71% {
    margin-top: 0px;
  }

  99.99% {
    margin-top: 0px;
  }

  100% {
    margin-top: -270px;
  }
}
.flip div:first-child {
  animation: show 7s linear infinite;
}
.flip > div > div {
  display: inline-block;
  height: 120px;
}
@media (min-width: 768px) {

  .flip > div > div {
    padding-left: 30px;
  }
}

@keyframes move {

  from {
    transform: translate(0%, 0%);
  }

  to {
    transform: translate(-100%, 100%);
  }
}

.star {
  animation: move 20s linear infinite;
}
@keyframes dash {

  to {
    stroke-dashoffset: -227.62;
  }
}
.shootingStar {
  animation: dash 0.7s ease;
}
@media (min-width: 768px) {

  .md\:h-96 {
    height: 24rem;
  }

  .md\:w-96 {
    width: 24rem;
  }

  .md\:pl-\[30px\] {
    padding-left: 30px;
  }

  .md\:text-5xl {
    font-size: 3rem;
    line-height: 1;
  }

  .md\:leading-loose {
    line-height: 2;
  }

  .md\:tracking-\[0\.5em\] {
    letter-spacing: 0.5em;
  }
}