.heart-label {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transform: scale(5);
}

.heart-label .heart-svg {
  width: 10px;
  position: relative;
  z-index: 2;
}

.heart-label .heart-path {
  fill: #FFFFFF;
  stroke-width: 45px;
  stroke: #999999;
  stroke-dasharray: 1600;
  stroke-dashoffset: 0;
  stroke-linecap: round;
}

.heart-label .heart-check[type="checkbox"] {
  display: none;		  
}

.heart-label .heart-check[type="checkbox"]:checked + .heart-svg .heart-path {
  animation: .8s draw-heart linear forwards;
}

@keyframes draw-heart {
  0% {
    stroke-dashoffset: 1600;
    stroke: #FA6A93;
  }
  80% {
    stroke-dashoffset: 0;
    fill: #FFF;
  }
  100% {
    stroke-dashoffset: 0;
    fill: #FA6A93;
    stroke: #FA6A93;
  }
}

.heart-label .heart-circle,
.heart-label .heart-circle:after, 
.heart-label .heart-circle:before {
  display: inline-block;
  width: 2.5px;
  height: 2.5px;
  border-radius: 50%;
  background-color: transparent;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotateZ(45deg) scale(0);
  
  box-shadow: 0 -20px 0 #FA6A93, 
        0 20px 0 #FA6A93, 
        -20px 0 0 #FA6A93, 
        20px 0 0 #FA6A93;
}

.heart-label .heart-circle:after {
  content: '';
  width: 3px;
  height: 3px;
  transform: translate(-50%, -50%) rotateZ(30deg) scale(0);

}

.heart-label .heart-circle:before {
  content: '';
  width: 3.5px;
  height: 3.5px;
  transform: translate(-50%, -50%) rotateZ(60deg) scale(0);

}



.heart-label .heart-check[type="checkbox"]:checked ~ .heart-circle {
  animation: .5s blink ease-in-out forwards .8s; 
}

.heart-label .heart-check[type="checkbox"]:checked ~ .heart-circle:after {
  animation: .7s blink-after ease-in-out forwards .6s; 
}

.heart-label .heart-check[type="checkbox"]:checked ~ .heart-circle:before {
  animation: .7s blink-before ease-in-out forwards .6s; 
}

@keyframes blink{
  0% {
    transform: translate(-50%, -50%) rotateZ(45deg) scale(.5);
    opacity: .9;
  }
  70% {
    transform: translate(-50%, -50%) rotateZ(45deg) scale(1);
    opacity: .9;
  }

  100% {
    transform: translate(-50%, -50%) rotateZ(45deg) scale(1.1);
    opacity: 0;
  }
}

@keyframes blink-after{
  0% {
    transform: translate(-50%, -50%) rotateZ(30deg) scale(.5);
    opacity: 1;
  }
  70% {
    transform: translate(-50%, -50%) rotateZ(30deg) scale(1);
    opacity: 1;
  }
  100% {
    transform: translate(-50%, -50%) rotateZ(30deg) scale(1.1);
    opacity: 0;
  }
}

@keyframes blink-before{
  0% {
    transform: translate(-50%, -50%) rotateZ(60deg) scale(.5);
    opacity: 1;
  }
  70% {
    transform: translate(-50%, -50%) rotateZ(60deg) scale(1);
    opacity: 1;
  }

  100% {
    transform: translate(-50%, -50%) rotateZ(60deg) scale(1.1);
    opacity: 0;
  }
}

.heart-label .heart-check[type="checkbox"]:checked + .heart-svg {
  animation: 1s pop linear forwards;
}

@keyframes pop {
  0%, 70%, 100% {
    transform: scale(1);
  }
  
  80% {
    transform: scale(1.3);
  }
}

.heart-label .heart-check[type="checkbox"] + .heart-svg {
  animation: 1s popover linear forwards;
}

@keyframes popover {
  0% {
    transform: scale(1.3);
  }
  
  30%, 100% {
    transform: scale(1);
  }
}